import React from 'react';
import {useAuth0} from '@auth0/auth0-react';

const AdoptNow = () => {
  const {logout} = useAuth0();
  return (
    <button
      onClick={() =>
        logout({
          returnTo: 'https://reneshen0328.github.io/dog-adoption-frontend',
        })
      }
    >
      Log Out
    </button>
  );
};

export default AdoptNow;
